import '@fancyapps/fancybox';


$('.popup-gallery [data-fancybox]').fancybox({
  backFocus: false
});



$('.show-photo [data-fancybox]').fancybox({
  backFocus: false
});


















// 
$('.slider__overlay-01').mouseover(function() {
  $('.slide__border-01').toggleClass('active');
  $('.slider__name-document-01').toggleClass('active');
});


$('.slider__overlay-01').mouseout(function() {
  $('.slide__border-01').removeClass('active');
  $('.slider__name-document-01').removeClass('active');
});

